@import url(https://fonts.googleapis.com/css2?family=Hind+Vadodara:wght@300&family=Roboto&family=Zen+Maru+Gothic:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
*{
    padding: 0;
    margin: 0;
}

.header-main{   
    background-color: #fff159;
	color: white;
	height: 80px;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}


.header-main .logo img{
    width: 200px;
    margin-left: 5px;
    margin-top: 7px;
}
.nav{
    width: 900px;
    display: flex;
}
.nav input{
    width: 100%;
    height: 31px;
    margin-top: 10px;
}
.nav input:-ms-input-placeholder{
   
    font-size: 17px;
    margin-left: 15px;
    padding-left: 5px;
}
.nav input::placeholder{
   
    font-size: 17px;
    margin-left: 15px;
    padding-left: 5px;
}

.nav img{
    width: 30px;
}

.nav button{
    margin-top: 10px;
    height: 35px;
    width: 40px;
    cursor: pointer;
}

.nav input:focus{
    font-size: 17px;
    padding-left: 5px;
}


.nav input:active{
    outline:none;
}


*{
    font-family: 'Roboto';
}
.container-producto{
    margin: auto;
    width: 900px;
    height: 200px;
    background-color: #fff;
    box-shadow: 0px 5px 5px rgb(43, 44, 44);
    border-bottom: 1px ;
    display: flex;
    margin-top: 10px;
}

.container-producto .image-container{
    width: 200px;
}

.container-producto .image-container img{
    width: 120px;
    height: 120px;
    margin-top: 30px;
    margin-left: 40px;
}

.container-producto .item-info{
    text-decoration: none;
    color: #000;
}

.container-producto .item-info h2{
    text-decoration: none;
    font-size: 20px;
    margin-top: 10px;
}

a{
    text-decoration:none;
}
.product-list-container{
    width:800px;
    margin: auto;
    margin-top:20px;
}
p{
    margin-top: 650px ;
    background-color: rgb(177, 171, 171);
    font-size: 15px;
    text-align: center;
    width:100%;
}
.detail-container{
    width: 1000px;
    margin:auto;
    box-shadow: 0px 2px 5px rgb(43, 44, 44);
}
.image-info-container{
    margin: 10px;
    flex-grow: 2;
    display: flex;
    justify-content: center;
    align-items: center;

}

.description-container{
    margin-left: 140px;
    margin-right: 140px;
    display: flex;
}

.description-container h3{
    font-family: 'Roboto', sans-serif;
    color: #4b4b4b;
}

.image-info-container img{
    width: 300px;    
}

.info-container{
    font-size: 20px;
    margin-left: 10px;
    margin-bottom: 300px;
}

.detail-container{
    font-size: 15px;
    
}

.details {
    display: flex;
    flex-direction: row;
}



.prices button{
    width: 300px;
    font-size: 30px;
    height: 50px;
    background-color: #1F6AF0;
    color: white;
    margin-top: 30px;
}

.prices button:hover{
    transform: scale(1.01);
}
